<template>
	<div id="gohome-order-detail">
		<public-header title="订单详情"></public-header>
		<div class="content">
			<div class="goods-info margin-10 padding-15 bg-default radius-3">
				<div class="flex-lr van-hairline--bottom padding-10-b">
					<div class="flex">
						<div class="number">#{{order.id}}</div>
						<div class="itag" :class="order.order_type_all.css" v-if="order.order_type_all">{{order.order_type_all.text}}</div>
					</div>
					<div class="font-14 c-danger">{{order.status_cn}}</div>
				</div>
				<div class="padding-15-tb flex-lr van-hairline--bottom">
					<div class="thumb">
						<img class="img-100" :src="order.goods.thumb" alt="">
					</div>
					<div class="info">
						<p>{{order.goods.name}}</p>
						<div class="flex-lr w-100">
							<div class="flex">
								<div class="c-danger font-16 font-500">¥{{order.final_fee}}</div>
								<div class="c-gray font-12 line-through margin-10-l">¥{{order.goods.oldprice}}</div>
							</div>
							<span class="c-gray">x{{order.num}}</span>
						</div>
					</div>
				</div>
				<div class="padding-15-tb van-hairline--bottom">
					<div class="flex-lr font-14">
						<div class="c-gray">总价</div>
						<div>￥{{order.final_fee}}</div>
					</div>
				</div>
				<div class="padding-15-t">
					<div class="flex-lr">
						<div class="c-gray">预计收入</div>
						<div class="price">￥{{order.final_fee}}</div>
					</div>
				</div>
			</div>
			<div class="margin-10 padding-15 bg-default radius-3 font-13">
				<div class="padding-15-b van-hairline--bottom font-14">预留信息</div>
				<van-row class="c-gray margin-10-t">
					<van-col span="6">下单顾客：</van-col>
					<van-col span="18">{{order.username}}</van-col>
				</van-row>
				<van-row class="c-gray margin-10-t">
					<van-col span="6">手机号：</van-col>
					<van-col span="18">{{order.mobile}}</van-col>
				</van-row>
			</div>
			<div class="margin-10 padding-15 bg-default radius-3 font-13">
				<div class="padding-15-b van-hairline--bottom font-14">订单信息</div>
				<van-row class="c-gray margin-10-t">
					<van-col span="6">订单编号：</van-col>
					<van-col span="18">{{order.ordersn}}</van-col>
				</van-row>
				<van-row class="c-gray margin-10-t">
					<van-col span="6">支付方式：</van-col>
					<van-col span="18">{{order.pay_type_cn}}</van-col>
				</van-row>
				<van-row class="c-gray margin-10-t">
					<van-col span="6">下单时间：</van-col>
					<van-col span="18">{{order.addtime_cn}}</van-col>
				</van-row>
				<van-row class="c-gray margin-10-t">
					<van-col span="6">支付时间：</van-col>
					<van-col span="18">{{order.paytime_cn}}</van-col>
				</van-row>
				<van-row class="c-gray margin-10-t">
					<van-col span="6">买家备注：</van-col>
					<van-col span="18" class="remark">{{order.buyremark ? order.buyremark : '无'}}</van-col>
				</van-row>
				<van-row class="c-gray margin-10-t" v-if="order.remark">
					<van-col span="6">卖家备注：</van-col>
					<van-col span="18" class="remark">{{order.remark}}</van-col>
				</van-row>
			</div>
			<div class="margin-10 btn-group1">
				<div class="btn-item bg-info" @click="onRemark()">备注</div>
				<div class="btn-item bg-danger" @click="onChangeStatus('cancel')" v-if="order.status < 5">取消</div>
				<template v-if="order.order_type == 'pintuan' && order.is_team == 1">
					<div class="btn-item bg-success" @click="onChangeStatus('team_success')" v-if="order.status == 2">立即成团</div>
					<div class="btn-item bg-danger" @click="onChangeStatus('team_cancel')" v-if="order.status == 2 || order.status == 3">取消成团</div>
				</template>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
		<van-popup v-model="remarkShow" position="bottom">
			<div class="popup-title">
				<span class="c-gray" @click="remarkShow = false">取消</span>
				<span class="c-info" @click="onRemark('submit')">确定</span>
			</div>
			<van-field
				v-model="remark"
				label="备注"
				type="textarea"
				placeholder="请输入备注"
				rows="5"
				class="margin-10-t"
			/>
		</van-popup>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData, setData} from "@/controller/funcCommon"
	import {changeOrderStatus} from "../controller/order.js"
	export default {
		data() {
			return {
				id: 123,
				order: {
					overtime: '',
					store: {},
					goods: {}
				},
				showPreLoading: true,
				remarkShow: false,
				remark: ''
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'manage/gohome/order/detail',
					data: {
						id: that.id
					},
					success: (res) => {
						that.order = that.util.extend(that.order, res.order);
					}
				})
			},

			onRemark(issubmit) {
				var that = this;
				if(issubmit == 'submit') {
					if(!that.remark) {
						that.util.$toast('请先输入备注');
						return;
					}
					setData({
						vue: that,
						url: 'manage/gohome/order/remark',
						data: {
							id: that.id,
							remark: that.remark
						},
						success: function(res) {
							that.util.$toast(res);
							that.remarkShow = false;
							that.order.remark = that.remark;
							that.order = Object.assign({}, that.order);
						}
					});
				} 
				that.remarkShow = !that.remarkShow;
			},
			onChangeStatus(type) {
				var that = this;
				changeOrderStatus({
					vue: that,
					type: type,
					from: 'detail',
					order: that.order
				})
			}
		},
		mounted(){
			if(this.$route.query && this.$route.query.id) {
				this.id = this.$route.query.id;
			}
			this.onLoad();
		}
	}
</script>

<style>
	#gohome-order-detail .goods-info .price{
		font-size: 18px;
		font-weight: 600;
		color: #FF2C4C;
	}
	#gohome-order-detail .goods-info .number{
		font-size: 18px;
		font-weight: 600;
	}
	#gohome-order-detail .goods-info .itag{
		font-size: 12px;
		margin-left: 10px;
		border-radius: 0px;
	}
	#gohome-order-detail .goods-info .label{
		color: #fff;
	}
	#gohome-order-detail .goods-info .label.label-info{
		background-color: #33aafc;
	}
	#gohome-order-detail .goods-info .label.label-warning{
		background-color: #f0ad4e;
	}
	#gohome-order-detail .goods-info .label.label-danger{
		background-color: #FF2C4C;
	}
	#gohome-order-detail .goods-info .thumb{
		width: 50px;
		height: 50px;
		border-radius: 3px;
		overflow: hidden;
		font-size: 0px;
	}
	#gohome-order-detail .goods-info .info{
		height: 50px;
		font-size: 14px;
		flex: 1;
		padding-left: 10px;
		display: flex;
		flex-flow: column nowrap;
		align-items: flex-start;
		justify-content: space-between;
	}
	#gohome-order-detail .content .remark{
		word-break: break-all;
		white-space: pre-wrap;
	}
	#gohome-order-detail .content .btn-group1{
		display: flex;
		align-items: center;
		justify-content: flex-end;
	}
	#gohome-order-detail .content .btn-group1 .btn-item{
		border: 1px solid;
		border-radius: 3px;
		padding: 5px 8px;
		margin-left: 10px;
		font-size: 12px;
	}
</style>


