import util from '@/config/util.js'
import router from '@/router'

function orderStatusChangeTips(type) {
	var types = {
		cancel: {
			tip: '确定取消订单吗',
			success_status: 7
		},
		refund_handle: {
			tip: '确定发起退款吗',
			success_status: 2
		},
		refund_status: {
			tip: '确定设置为已退款吗',
			success_status: 3
		},
		team_success: {
			tip: '确定立即成团吗',
			success_status: 3
		},
		team_cancel: {
			tip: '确定取消成团吗',
			success_status: 7 
		}

	}
	return types[type];
};

function changeOrderStatus(options) {
	var order = options.order;
	var type = options.type;
	var typeInfo = orderStatusChangeTips(type);
	var success_status = typeInfo.success_status;
	var postData = {
		id: order.id
	};
	if(type == 'team_success') {
		var url = 'manage/gohome/order/cancel';
	} else {
		if(type == 'team_cancel') {
			postData.team_cancel = 1;
			type = 'cancel';
		}
		var url = 'manage/gohome/order/' + type;
	}
	var params = {
		url: url,
		data: postData,
		confirm: typeInfo.tip,
		success: function(message) {
			util.$toast(message);
			if(options.from == 'detail') {
				if(type == 'refund_handle' || type == 'refund_status') {
					options.vue.order.refund_status = success_status;
				} else {
					options.vue.order.status = success_status;
				}
				options.vue.order = Object.assign({}, options.vue.order);
			} else {
				if(type == 'refund_handle' || type == 'refund_status') {
					options.vue.records.data[options.index].refund_status = success_status;
				} else {
					options.vue.records.data.splice(options.index, 1);
				}
			}
		}
	};
	util.jspost(params);
};

export {
	orderStatusChangeTips,
	changeOrderStatus,
}



